import React from "react";
import { Select } from "antd";
import { Script } from 'types';
import { useQuery } from 'react-query3';

const { Option } = Select;

export default function ScriptSelect(props) {

    const { data: scripts, isLoading } = useQuery<Array<Script>>(`/script`, {
        refetchOnWindowFocus: false,
    });

    return (
        <Select {...props} loading={isLoading}
            showSearch
            filterOption={(input, option) =>
                option.value.toLowerCase().indexOf(input.toLowerCase()) >= 0
            }>
            <Option key="none" value={""}>
                {null}
            </Option>
            {scripts?.map((option) => (
                <Option key={option?.id} value={option?.fullPath}>
                    {option?.fullPath}
                </Option>
            ))}
        </Select>
    );
}